.cart {
    display: flex;
    width: 100%;
  
    .cartContainer {
      flex: 6;
      background-color: #fcf5f5;
      // background-color: #fbfbfb;

  
     
  
      .datatableTitle {
        font-size: 24px;
        color: gray;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 20px 20px 10px 20px;

  
  
        .icon {
          margin-left: 5px;
        }
  
        .link{
          text-decoration: none;
          color: #7451f8;
          font-size: 14px;
          font-weight: 400;
          border: 1px solid #7451f8;
          padding: 5px 5px;
          margin-left: 10px;
          border-radius: 5px;
          cursor: pointer;
          &:hover {
            background-color: #6146c5;
            color: white;
          }
      
        }
      }
  
      .top {
        padding: 0px 20px 0px 20px;
        display: flex;
        gap: 20px;
  
        .left {
          flex: 6;
          // -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
          // box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
          border: 1px solid lightgray;
          // background-color: #ffffff;
          padding: 20px;
          position: relative;
  
          .editButton {
            position: absolute;
            top: 0;
            right: 0;
            padding: 5px;
            font-size: 12px;
            color: #7451f8;
            background-color: #7551f818;
            cursor: pointer;
            border-radius: 0px 0px 0px 5px;
            &:hover {
              background-color: #6246c5aa;
              color: white;
            }
          }
  
          .smallImg {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            object-fit: cover;
            margin-right: 10px;
            border: 1px solid lightgray;
            &:hover {
              border: 1px solid #7451f8;
              cursor: pointer;
            }
          }
  
          .smallAlt {
            width: 66px;
            height: 66px;
            // border-radius: 50%;
            object-fit: cover;
            margin-right: 10px;
            border: 1px solid lightgray;
            &:hover {
              border: 1px solid #7451f8;
              cursor: pointer;
            }
          }
  
          .item {
            display: flex;
            gap: 20px;
  
            .itemImg {
              width: 175px;
              height: 175px;
              border-radius: 50%;
              object-fit: cover;
              margin-right: 10px;
            }
  
            .altImg {
              width: 240px;
              height: 240px;
              // border-radius: 50%;
              object-fit: cover;
              margin-right: 10px;
            }
  
            .details {


              .itemTitle {
                margin-bottom: 10px;
                color: #555;
              }
  
              .detailItem {
                margin-bottom: 10px;
                font-size: 14px;
  
                .itemKey {
                  font-weight: bold;
                  color: gray;
                  margin-right: 5px;
                }
  
                .itemValue {
                  font-weight: 300;
                  color:#7860cf;        
                }
              }
            }
          }
        }
  
        .right {
          flex: 5;
          border: 1px solid lightgray;
          form {
            display: flex;
            flex-wrap: wrap;
            gap: 30px;
            justify-content: space-around;
  
            .formInput {
              width: 40%;
  
              label {
                display: flex;
                align-items: center;
                gap: 10px;
  
                .icon {
                  cursor: pointer;
                }
              }
  
              input {
                width: 100%;
                padding: 5px;
                border: none;
                border-bottom: 1px solid gray;
              }
            }
  
            button {
              width: 150px;
              padding: 10px;
              border: none;
              background-color: #6146c5;
              color: white;
              font-weight: bold;
              cursor: pointer;
              margin-top: 10px;
  
              &:disabled {
                background-color: rgba(3, 92, 92, 0.322);
                cursor: not-allowed;
              }
            }
          }
        }
      }
  
      .bottom {
        // -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
        // box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
        // background-color: white;
        border: 1px solid lightgray;
        padding: 20px 20px 0px 20px;
        margin: 10px 20px;
  
     
      }
  
      .title {
        font-size: 16px;
        color: gray;
        margin-bottom: 20px;
      }
  
      .listTitle {
        font-weight: 500;
        color: gray;
        margin-bottom: 5px;
      }
    }
  }