.datatable {
  height: 640px;
  padding: 20px 20px;
  
   button {
    // background-color: white;
    margin: 0px 5px;
    padding: 0px;
 
   }

   .MuiButton-root {
    color: #7451f8;
   }

   .Mui-checked {
    color: #7551f8 !important;
  }

  //  .MuiSvgIcon-root {
  //   color: #7451f8;
  //  }

 

 



  .datatableTitle{
    width: 100%;
    font-size: 24px;
    color: gray;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;

  

    .search {
      display: flex;
      align-items: center;
      border: 0.5px solid lightgray;
      padding: 3px !important;
      background: transparent !important;
      input {
        border: none;
        outline: none;
        &::placeholder {
          color: #7451f8;
          font-size: 14px;
        }
      }
  
    }

   

    .link{
      text-decoration: none;
      color: #7451f8;
      font-size: 14px;
      font-weight: 400;
      border: 1px solid #7451f8;
      padding: 5px 5px;
      margin-left: 10px;
      border-radius: 5px;
      cursor: pointer;
    
        &:hover {
          background-color: #6146c5;
          color: white;
          //   :not(#searchicon) {
           
          // }
       
          
     

      }
     
      .icon {
        font-size: 13px;
        color: #7451f8;
        padding: 0px 3px;
     
      
      }
      
    }
  }

  .cellWithImg {
    display: flex;
    align-items: center;

    .cellImg {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      object-fit: cover;
      margin-right: 20px;
    }
  }

  .cellWithStatus {
    padding: 0px;
    border-radius: 5px;

    &.Delivered , .InStock{
      // background-color: rgba(0, 128, 0, 0.05);
      color: green;
    }
    &.Limited , .Shipped {
      // background-color: rgba(255, 217, 0, 0.15);
      color: goldenrod;
    }
    &.Pending , .OutOfStock{
      // background-color: rgba(255, 0, 0, 0.05);
      color: crimson;
    }
  }


  .cellAction {
    display: flex;
    align-items: center;
    gap: 15px;

    .viewButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: rgb(0, 139, 97);
      // border: 1px solid rgba(0, 0, 139, 0.596);
      cursor: pointer;
    }

    .updateButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: rgb(0, 139, 97);
      // border: 1px solid rgb(0, 139, 97);
      cursor: pointer;
    }
    
    .deleteButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: crimson;
      // border: 1px solid rgba(220, 20, 60, 0.6);
      cursor: pointer;
    }

    .icon {
      font-size: 18px;
      color: #7451f8;
    }
  }
}




